.loader {
  width: 8px;
  height: 8px;
  background: #fff;
  border-radius: 50%;
  animation: wave 1s ease-in infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes wave {
  0% {
    box-shadow: 0 0 #fff, 0 0 0 20px rgba(255, 255, 255, .2), 0 0 0 40px rgba(255, 255, 255, .6), 0 0 0 60px rgba(255, 255, 255, .4), 0 0 0 80px rgba(255, 255, 255, .2);
  }

  100% {
    box-shadow: 0 0 0 80px rgba(255, 255, 255, 0), 0 0 0 60px rgba(255, 255, 255, .2), 0 0 0 40px rgba(255, 255, 255, .4), 0 0 0 20px rgba(255, 255, 255, .6), 0 0 #fff;
  }
}

.breed-select {
  margin-bottom: 30px;
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .3);
  position: fixed;
  top: 0;
  left: 0;
}

.cat-info {
  display: flex;
}

.cat-list {
  margin-left: 20px;
}

.cat-name {
  font-size: larger;
  font-weight: bold;
}

.cat-temp {
  font-weight: bold;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

